import { AfterViewInit, Component, Inject, OnInit } from '@angular/core';
import { IFondo } from '../../models/fondo';
import { FondosService } from '../../service/fondos.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AppAlertService } from 'app/shared/service/app/alert.service';
import { JedsMenuService } from '@shared/service/app/menu.service';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
@Component({
    selector: 'app-save-fondo',
    templateUrl: './save.component.html',
    styleUrls: ['./save.component.scss']
})
export class SaveComponent implements OnInit {
    columns = ['descripcion', 'activo', 'acciones']
    form: FormGroup;
    fondo: IFondo;
    title: string;
    isChecked = 0;
    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private _fondosService: FondosService,
        public alert: AppAlertService,
        public menu: JedsMenuService,
        public dialogRef: MatDialogRef<SaveComponent>
    ) {
        this.fondo = this.data ? this.data : null;
        this.title = this.fondo?.idfondo ? 'Editar Fondo' : 'Nuevo Fondo';
    }

    ngOnInit(): void {
        this.newForm();
    }

    newForm() {
        this.form = new FormGroup({
            descripcion: new FormControl((this.fondo ? this.fondo.descripcion : ''), []),
            activo: new FormControl((this.fondo ? (this.fondo.activo ? 1 : 0) : 1)),    //Se hace un if ternario porque en un momento el valor de activo se vuelve literalmente 'si'.
        });
    }

    onSubmit() {
        if (!this.form.valid) {
            return this.alert.warning('Revise Los Datos Ingresados');
        } else {
            this.fondo = {
                ...this.fondo,
                descripcion: this.form.get('descripcion').value,
                activo: this.form.get('activo').value

            }
            if (this.fondo.idfondo) {
                this._fondosService.updateFondo(this.fondo)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.onCloseMenu();
                            }
                        }
                    });
            } else {
                this._fondosService.saveFondo(this.fondo)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.onCloseMenu();
                            }
                        }
                    });
            }
        }
    }

    onCloseMenu() {
        this.dialogRef.close(false)
    }
}